export function isTrustworthyDevice() {
    return isTrustworthyMobile() || isTrustworthyDesktop();
}

function isTrustworthyMobile() {
    return isTrustworthyAndroid() || isTrustworthyIphoneOrIpad();
}

function isTrustworthyAndroid() {
    return navigator.userAgent.indexOf('Android') !== -1 && (navigator.platform.indexOf('Linux') !== -1 || navigator.platform.indexOf('Android') !== -1);
}

function isTrustworthyIphoneOrIpad() {
    return (isTrustworthyIphone() || isTrustworthyIpad()) && isNotXcodeSimulator();
}

function isTrustworthyIphone() {
    return navigator.userAgent.indexOf('iPhone') !== -1 && navigator.platform.indexOf('iPhone') !== -1;
}

function isTrustworthyIpad() {
    return navigator.userAgent.indexOf('iPad') !== -1 && navigator.platform.indexOf('iPad') !== -1;
}

function isNotXcodeSimulator() {
    var webGlContext = document.createElement('canvas').getContext('webgl');
    var rendererExtension = webGlContext.getExtension('WEBGL_debug_renderer_info');
    var webGlRenderer = webGlContext.getParameter(rendererExtension.UNMASKED_RENDERER_WEBGL);
    return webGlRenderer.indexOf('Apple GPU') === -1 && webGlRenderer.indexOf('Apple Software Renderer') === -1;
}

function isTrustworthyDesktop() {
    return isTrustworthyWindows() || isTrustworthyMacos();
}

function isTrustworthyWindows() {
    return navigator.userAgent.indexOf('Windows') !== -1 && navigator.platform.indexOf('Win32') !== -1;
}

function isTrustworthyMacos() {
    return navigator.userAgent.indexOf('Macintosh') !== -1 && navigator.platform.indexOf('MacIntel') !== -1;
}