let state = {
    currentPositionAvgAccuracy: -1,
    lastCurrentPosition: {},
    watchPositionAvgAccuracy: -1,
};

export function testCurrentPositions(positions) {
    console.log(positions.length);
    
    if(positions.length > 0){
    
        hasTimeRefDifferences(positions);
    
        hasBadVelocity(positions);
    
        console.log("real ref jitters", getRealRefJitters(positions));
        
        if(hasBigPositionJumps(positions)) {
            return _failMessage(`Current position - Failed (big jumps detected)`);
        }
        
        const noJitterCount = getNoJittersCount(positions);
        // const halfSamplesCount = Math.ceil(positions.length/2);
        const allSamplesCount = positions.length;
        
        if(noJitterCount === allSamplesCount) {
            return _failMessage(`Current position - Failed (no jitter in ${allSamplesCount} samples)`);
        }
        // else if(noJitterCount >= halfSamplesCount) {
        //     return _failMessage(`Current position - Failed (no jitter in ${noJitterCount}/${allSamplesCount} samples)`);
        // }
    
        let currentPositionAvgAccuracy = calcAverageAcc(positions);
        state = {...state,
            currentPositionAvgAccuracy: currentPositionAvgAccuracy,
            lastCurrentPosition: {
                coords: {...positions[positions.length - 1].coords},
                timestamp: positions[positions.length - 1].timestamp
            }
        };
        console.log("getCurrentPosition acc avg", currentPositionAvgAccuracy);
    
        return _successMessage(`Current position - Passed (${allSamplesCount} samples)`);
        
    }
    
    return _failMessage(`Current position test - Failed (no geo data)`);
}

function _successMessage(message) {
    return _testResultMessage({
        message: message,
        passed: true,
        testFinished: true
    });
}
function _failMessage(message) {
    return _testResultMessage({
        message: message,
        passed: false,
        testFinished: true
    });
}
function _testResultMessage({message, passed, testFinished}) {
    return {
        message: message,
        passed: passed,
        testFinished: testFinished
    };
}

function getNoJittersCount(positions) {
    const jitters = getJitters(positions);
    console.log("jitters", jitters);
    return jitters.filter((j) => j === 0).length;
}

function getJitters(positions) {
    return positions.map((position, index) => {
        if (index !== 0) {
            return positions[index].timestamp - positions[index - 1].timestamp;
        }
        return 0;
    });
}

function getRealRefJitters(positions) {
    return positions.map((position, index) => {
        if (index !== 0) {
            return positions[index].timestampRef - positions[index - 1].timestampRef;
        }
        return 0;
    });
}


export function testWatchPositions(positions) {
    
    if(positions.length > 0){
    
        hasTimeRefDifferences(positions);
    
        hasBadVelocity(positions);
    
        console.log("real ref jitters", getRealRefJitters(positions));
        
        if(hasBigPositionJumps(positions)) {
            return _failMessage(`Watch position - Failed (big jumps detected)`);
        }
        
        const noJitterCount = getNoJittersCount(positions);
        if(noJitterCount === positions.length) {
            return _failMessage(`Watch position test - Failed (no jitter in ${positions.length} samples)`);
        }
    
        if(hasBadAccuracy(positions)) {
            return _failMessage(`Watch position - Failed (accuracy)`);
        }
        
        if(hasNoLatLongAccVariations(positions)) {
            return _failMessage(`Watch position - Failed (not enough variation in lat, long, acc)`);
        }
    
        let watchPositionAvgAccuracy = calcAverageAcc(positions);
        state = {...state, watchPositionAvgAccuracy: watchPositionAvgAccuracy};
        console.log("watchPosition acc avg", watchPositionAvgAccuracy);
        
        if(accuracyNotImproved(state)) {
            return _failMessage(`Accuracy didn't improve`);
        }
        
        return _successMessage(`Watch position test - Passed (${positions.length} samples)`);
    }
    
    return _failMessage(`Watch position test - Failed (no geo data)`);
}

export function hasBigPositionJumps(positions) {
    try {
        if(positions.length > 0){
            console.log("big Positions start");
            
            const distances = positions.map((position, index, positions) => {
                if(index !== 0){
                    
                    const lat1 = positions[index - 1].coords.latitude;
                    const lon1 = positions[index - 1].coords.longitude;
                    const lat2 = position.coords.latitude;
                    const lon2 = position.coords.longitude;
    
                    return calcCrow(lat1, lon1, lat2, lon2);
                }
                return 0;
            });
        
            console.log("distances", distances);
            
            const bigJumps = _getInvalidDistances(distances, 10);
            console.log("bigJumps", bigJumps);
            
            return bigJumps.length > 0;
        }
    } catch (e) {
        return false;
    }
    
    return false;
}

function calcCrow(lat1, lon1, lat2, lon2) {
    if ((lat1 === lat2) && (lon1 === lon2)) {
        return 0;
    }
    
    const R = 6371; // km
    const dLat = toRad(lat2-lat1);
    const dLon = toRad(lon2-lon1);
    const _lat1 = toRad(lat1);
    const _lat2 = toRad(lat2);
    
    const a = Math.sin(dLat/2) * Math.sin(dLat/2) +
        Math.sin(dLon/2) * Math.sin(dLon/2) * Math.cos(_lat1) * Math.cos(_lat2);
    const c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1-a));
    return R * c;
}

// Converts numeric degrees to radians
function toRad(value) {
    return value * Math.PI / 180;
}

function calculateVelocity(lat2, lon2, lat0, lon0, t2, t0, index) {
    const deltaX = calcCrow(lat2, lon2, lat0, lon0) * 1000; //in m
    const deltaT = (t2 - t0) / 1000; //in sec
    // console.table([["lat0", "lon0"],[lat0, lon0],["lat2", "lon2"], [lat2, lon2]] );
    // console.info("deltaX", deltaX, " deltaT", deltaT);
    let velocity = calcVelocity(deltaX, deltaT);
    console.log("velocity", velocity, "m/s", " #", index);
    return velocity;
}


function calcVelocity(deltaX, deltaT){
    if(deltaT && deltaX){
        return deltaX/deltaT;
    }
    return 0;
}

// assumes valid periods of time
function _getInvalidDistances(distances, validDistance) {
    return distances.filter((distance) => distance > validDistance);
}


export function hasTimeRefDifferences(positions) {
    if(positions.length > 0){
        
        const timeDiffs = positions.map((position, index, positions) => {
            const t2Ref = position.timestampRef;
            const t2 = position.timestamp;
            
            let tDiff = (t2Ref - t2)/1000;
            console.log(t2Ref, "vs", t2, tDiff, "sec diff");
            return tDiff;
        });
        
        console.log("timeDiffs", timeDiffs);
    }
}

export function hasBadVelocity(positions) {
    try {
        if(positions.length > 0){
            
            const lat0 = positions[0].coords.latitude;
            const lon0 = positions[0].coords.longitude;
            const t0 = positions[0].timestamp;
            
            const velocities = positions.map((position, index, positions) => {
                if(index !== 0){
                    
                    const lat2 = position.coords.latitude;
                    const lon2 = position.coords.longitude;
                    const t2 = position.timestamp;

                    return calculateVelocity(lat2, lon2, lat0, lon0, t2, t0, index);
                }
                return 0;
            });
    
            const speeds = positions.map((position, index, positions) => {
                return positions.coords.speed;
            });
            
            console.log("velocities", velocities);
            console.log("speeds", speeds);
            
            return false;
        }
    } catch (e) {
        return false;
    }
    
    return false;
}


function hasBadAccuracy(positions) {
    try {
        if(positions.length >= 10){
            const bad1mAccuracies = positions.filter((position) => {
                return position.coords.accuracy === 1
            });
            
            console.log("bad1mAccuracies", bad1mAccuracies);
            return bad1mAccuracies.length === positions.length;
        }
        
    } catch (e) {
        return false;
    }
    return false;
}

function hasNoLatLongAccVariations(positions) {
    try {
        if(positions.length >= 10){
            const constLat = [...new Set(positions.map((position) => position.coords.latitude))];
            const constLong = [...new Set(positions.map((position) => position.coords.longitude))];
            const constAcc = [...new Set(positions.map((position) => position.coords.accuracy))];

            console.log("constLat", constLat);
            console.log("constLong", constLong);
            console.log("constAcc", constAcc);

            return constLat.length === 1 && constAcc.length === 1 && constLong.length === 1;
        }
    } catch (e) {
        return false;
    }
    
    return false;
}

export function calcAverageAcc(positions) {
    try {
        if(positions.length > 0){
            
            // const lat0 = positions[0].coords.latitude;
            // const lon0 = positions[0].coords.longitude;
            // const t0 = positions[0].timestamp;
            
            const accuracies = positions.map((position, index) => {
                return position.coords.accuracy;
            });
            
            console.log("accuracies", accuracies);
            
            const sum = accuracies.reduce((acc, a) => acc + a, 0);
            return sum/accuracies.length;
        }
    } catch (e) {
        return -1;
    }
    
    return -1;
}

function accuracyNotImproved({currentPositionAvgAccuracy, watchPositionAvgAccuracy}) {
    console.log("Avg Acc current", currentPositionAvgAccuracy, "vs watch", watchPositionAvgAccuracy);
    return currentPositionAvgAccuracy < watchPositionAvgAccuracy;
}

